import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import ConcertLocation from './ConcertLocation';
import { Link } from "react-router-dom";
import Avatar from '@material-ui/core/Avatar';
import { LocationOn, Person, Warning } from '@material-ui/icons';



function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  title: {
    fontSize: '20px',
    fontFamily: "NanumSquare",
    backgroundColor: '#fafafa',
    color: 'black',
  },
  box: {
    width: '1200px',
    height: '500px'
  },
  image: {
    width: theme.spacing(30),
    height: theme.spacing(30),
    display: 'flex',
    float: 'left',
    marginLeft: '100px',
    marginBottom: '20px',
    '&:hover': {
      transform: 'scale(1.05)',
    },
    '& > *': {
      margin: theme.spacing(1),
    },
  },
  name: {
    height: '250px',
    fontSize: '30px',
    fontWeight: 'bold',
    fontFamily: 'NanumSquare',
    float: "left",
    marginLeft: '100px',
    marginTop: '50px',
    display: 'block',
    color: 'black',
    '&:hover': {
      transform: 'scale(1.05)',
    }
  },
  info: {
    marginTop: '20px',
    display: 'block',
    fontSize: '20px',
    fontFamily: "NanumSquare",
    float: 'left',
    marginLeft: '50px',
  },
  place: {
    fontWeight: 'bold',
    fontSize: '18px',
    fontFamily: 'NanumSquare',
  },
  warning: {
    color: 'red',
    fontWeight: 'bold',
    fontSize: '23px',
    position: 'absolute',
    marginLeft: '340px',
  }
  
}));

const ConcertTab = ({place, artistList}) => {
  const classes = useStyles();
  const [value, setValue] = useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };


    return (
      <div className={classes.root}>
        <AppBar position="static">
          <Tabs variant="fullWidth" value={value} onChange={handleChange} aria-label="simple tabs example" centered>
            <Tab className={classes.title} icon={<Person />} label=" " {...a11yProps(0)} />
            <Tab className={classes.title} icon={<LocationOn />} label=" " {...a11yProps(1)} />
            <Tab className={classes.title} icon={<Warning />} label=" " {...a11yProps(2)} />
          </Tabs>
        </AppBar>
        <TabPanel className={classes.box} value={value} index={0}>
          {
            artistList !== undefined ? (artistList.map((artist) => {
              return (
                <>
                  <Link to={"/streaming/artist/" + artist.artistId}>
                    <Avatar className={classes.image} src={artist.imageUri} alt=""/>
                  </Link>
                  <Link to={"/streaming/artist/" + artist.artistId}>
                    <div className={classes.name}>{artist.artistName}</div>
                  </Link>                  
                  <div className={classes.info}>{artist.description}</div>
                </>
              )
            }))
            :
            <span>loading...</span>
          }


        </TabPanel>
        <TabPanel value={value} index={1}>
        {
          place !== undefined ? (place.map((concertplace) => {
            return (
              <>
                <p className={classes.place}> : {concertplace}</p>
                <ConcertLocation place={concertplace}/>
              </>
            )
          }))
          :
          <span>loading...</span>
        }
        </TabPanel>
        <TabPanel className={classes.box} value={value} index={2}>
          <h5>   </h5>
          <span></span>
          <p>
          -       (   )        .
          <br></br>
                    .
          </p>
          
          <span></span>
          <p>
          -    ()  4~5      . (/   )
          <br></br>
          -           . (       .)
          <br></br>
          -         ,     . (CJ,   1 )
          </p>
          <h5>/ </h5>
          <p>
          -         // .
          <br></br>
          -     12     .
          <br></br>
          -           ,     .
          (* ,                .)
          <br></br>
          -                 .
          <br></br>
          -           . (          . )
          <br></br>
          -      .
          (  7    10     .)
          </p>
          <br></br>
          <br></br>
          <footer className={classes.warning}>
            Win:G     .
          </footer>
        </TabPanel>
      </div>
    );

}

export default ConcertTab;